 -->
<template>
    <div class="login_wrapper">
      <Header></Header>

      <div class="login_content">
        <div class="login_page">
          <div class="title">欢迎登录</div>
          <el-form :model="loginInfo" :rules="rules" ref="loginInfo" >
            <el-form-item prop="email">
              <el-input v-model="loginInfo.email" placeholder="邮箱账号" autofocus="autofocus"  @keyup.enter.native="submitForm"></el-input>
            </el-form-item>
            <el-form-item prop="passwd">
              <el-input v-model="loginInfo.passwd" type='password' placeholder="登录密码（8-20位字母数字组合）"  @keyup.enter.native="submitForm"></el-input>
            </el-form-item>
            <el-form-item class="login_form_btn">
              <el-button :loading="btnLoad" type="primary" @click="submitForm">登录</el-button>
            </el-form-item>
          </el-form>
          <div class="tip">
            <div class="forget_password"> <a href="javascript:;" @click="goTo('reset')">忘记密码?</a></div>
          </div>
        </div>
      </div>
      <section class="market_detail_wrapper">

      </section>
      <user-foot></user-foot>
    </div>
</template>

<script>
  import Header from '@/components/header/header'
  import UserFoot from '@/components/userFoot/userFoot'
  import { vaildEmail,vaildPassword} from '@/utils/validate'

  export default {
   data() {
     return {
      loginInfo: {
        email: '',
        passwd: ''
      },
      btnLoad:false,
      rules: {
        email: [
          { required: true, message: '账号为必填项', trigger: 'blur' }
        ],
        passwd: [
          { required: true, message: '密码为必填项', trigger: 'blur' }
        ]
      }
     }
   },
   methods: {
     // 登录提交
    submitForm() {
      if(!this.btnLoad){
        if(!vaildEmail(this.loginInfo.email)){
          this.$message.info('请输入正确格式的邮箱')
          return false
        }
        if(!vaildPassword(this.loginInfo.passwd)){
          this.$message.info('请输入正确格式的密码')
          return false
        }

        const loginInfo = {
          "user":this.loginInfo.email,
          "passwd":this.loginInfo.passwd,
        }
        this.btnLoad = true
        this.$store.dispatch('login', loginInfo).then(res=>{
          if(res.data.result == 0){
            this.btnLoad = false
            this.$message({message:'登录成功', type:'success', duration:1000})
            this.$router.replace({path: this.$route.query.redirect || '/'});
          }else{
            this.btnLoad = false
          }
        }).catch(err => {
          this.btnLoad = false
        })
      }
    },
    /**
     * 跳转页面
     */
    goTo(str){
      this.$router.replace('/'+str)
    }

   },
   components: {
    Header,UserFoot
   }
  }
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/style/base.scss';

  .login_wrapper{
    height: 100%;
    width: 100%;
    min-width: 1280px;
    padding-top: 47px;
    box-sizing: border-box;
    .login_content{
      width: 100%;
      height: calc(100% - 180px);
      background-color:$module_bg_color;
      position: relative;
      .login_page{
        height: 340px;
        width: 80%;
        max-width: 480px;
        min-width: 400px;
        position: absolute;
        top:50%;
        left: 50%;
        margin-top: -200px;
        margin-left: -190px;
        background-color: $light_font_color;
        padding:30px 50px;
        border-radius: 5px;
        box-sizing: border-box;
        .title{
          width: 100%;
          height: 40px;
          line-height: 40px;
          font-size: $font20;
          letter-spacing: 2px;
          color: $light_title_color;
          text-align: center;
          margin-bottom: 20px;
        }
        .login_form_btn{
          margin-bottom: 10px;
          .el-button{
            width: 100%;
            background-color: $user_btn_bg_btn;
            border-radius: 3px;
            font-size: $font14;
            letter-spacing: 1px;
            margin-top: 10px;
          }
        }
        .tip{
          display: flex;
          div{
            width: 100%;
            height: 20px;
            line-height: 20px;
            color: $border_active_color;
          }
          .forget_password{
            text-align: right;
            a{
              color: $active_font_color;
              font-size: $font14;
            }
          }
        }
      }
    }
    
  }
</style>



